}

#page4 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  z-index: 99;
  height: 100vh;
  width: 100vw;
  background-color: transparent;
  text-align: center;
  background-color: #e4e0dd;
}

#page4 > h1 {
  font-weight: 600;
  font-size: 2.7vw;
}

#page4 > h4 {
  margin-top: 20px;
  font-weight: 500;
  color: #646464;
  font-size: 1vw;
}

#page5 {
  position: relative;
  height: 100vh;
  width: 100vw;
  background-color: #fff;
}
#center-page5 {
  display: flex;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 70%;
  width: 75%;
  background-color: #f6f3ef;
}
#left-page5 {
  height: 100%;
  width: 50%;
}
#left-page5 > svg {
  scale: 0.7;
}
#right-page5 {
  display: flex;
  align-items: start;
  gap: 30px;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  width: 50%;
  padding-left: 60px;
}
#right-page5 > h1 {
  font-weight: 600;
}
#right-page5 > h4 {
  font-weight: 500;
  width: 70%;
  color: #96969685;
}
#right-page5 > button {
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  background-color: #000;
  color: #fff;
}
